import React from 'react'

const Category = (props) => {
    console.log(props)
    if (props.category === undefined) {
        return <div/>
    }
    const categoryStyle = {
        paddingTop: 10,
        paddingLeft: 2,
        border: 'solid',
        borderWidth: 1,
        marginBottom: 5
    }
    
    return (

        < div style={categoryStyle}>
            <p>
                {props.category.name}
            </p>

        </div>
    )
}

export default Category



