import React, { Component } from 'react'
import { connect } from 'react-redux'
import { BrowserRouter as Router, Route } from 'react-router-dom'

import LoginBar from './component/LoginBar'
import Category from './component/Category'
import Home from './component/Home'
import { getCategories } from './reducers/category'


class App extends Component {

  constructor() {
    super()
    this.state = {
    }
  }

  componentWillMount = async () => {
    await this.props.getCategories()
  }

  render() {

    return (
      <div>
        <LoginBar />

        <Router>
          <div>
            <HomePage categories={this.props.categories} />
            <CategoryPage categories={this.props.categories} />
          </div>
        </Router>
      </div>
    )
  }
}

const HomePage = ({ categories }) => (
  <Route exact path="/" render={() =>
    <Home categories={categories} />}
  />
)
const CategoryPage = ({ categories }) => {

  const categoryById = (categoryid) => {
    return (
      categories.find(category => Number(category.categoryid) === Number(categoryid))
    )
  }
  return (
    <Route exact path="/category/:categoryid" render={({ match, history }) =>
      <Category category={categoryById(match.params.categoryid)}
      />}
    />
  )
}

const mapStateToProps = (state) => {
  return {
    categories: state.categories
  }
}

export default connect(
  mapStateToProps,
  { getCategories }

)(App)


